<template>
  <div class="userList">
    <bread></bread>
    <table>
      <tr>
        <th style="width: 20em">头像/名称 <span class="people_info">(点击名称查看详情)</span></th>
        <th>类型</th>
        <th>所属人名称</th>
        <th>状态</th>
        <th>创建时间</th>
        <th>到期时间</th>
        <th>操作</th>
      </tr>
      <tr class="td" v-for="(item,key) in data">
        <td>
          <a href="javascript:void(0)" class="nickname" @click="seeInfo(item)">
            <img :src="'https://weapp.xiguawenhua.com/'+ item.pic" width="40px"  style="border-radius: 50%;margin-right: 1em" />
            <span style="display: inline-block;width: 100px;text-align: left;color:#2277da">
              {{item.name}}
            </span>
          </a>
        </td>
        <td>{{item.type}}</td>
        <td>{{item.username}}</td>
        <td>{{item.is_publish}}</td>
        <td>{{item.create_time}}</td>
        <td>{{item.use_time}}</td>
        <td>
          <i-switch size="large" v-model="item.is_forbidden" :value="item.is_forbidden" @on-change="change(item,key)" true-value="0" false-value="1">
            <span slot="open">启用</span>
            <span slot="close">禁用</span>
          </i-switch>
        </td>
      </tr>
    </table>
    <p v-if="data.length===0" class="td_data">
      {{msg}}
    </p>
    <div class="ter_page">
      <Page :total="num" :page-size-opts="[10,20,30,40]" :page-size="currentPage" size="small" show-total show-sizer @on-change="pageNnumber" @on-page-size-change="pageRow">
      </Page>
    </div>
  </div>
</template>
<script>
  import bread from '../bread.vue'
  import page from '../page.vue'
  export default {
    data() {
      return {
        msg: "正在加载....",
        data: [],
        status: '0',
        //      当前页码
        currentPage: 10,
        //      每页条数
        current: 1,
        //      总条数
        num: 0,
      }
    },
    methods: {
      remove(index) {
        //        this.data1.splice(index,1);
      },
      seeInfo(index) {
        console.log(index.id)
        let url = "appDetail/" + index.id
        this.$router.push({
          path: url
        })
      },
      change(index) {
        let app = "app"
        this.$Modal.confirm({
          title: '是否开启',
          content: '<p>确定要开启或关闭该用户吗</p>',
          onOk: () => {
            this.post("system/Info/forbiddenById", {
              id: index.id,
              type: app,
              is_forbidden: index.is_forbidden
            }).then(res => {
              if(res.data.code === 10000) {
                const content = res.data.msg
                this.$Modal.success({
                  title: "是否开启",
                  content: content
                });
              } else {
                const content = res.data.msg
                this.$Modal.error({
                  title: "是否开启",
                  content: content
                });
              }
            });
          },
        });
      },

      getAppList(currentPage, current) {
        this.post("system/Info/getUserAppList", {
          page: this.current,
          limit_num: this.currentPage,
        }).then(res => {
          if(res.data.code === 10000) {
            this.data = res.data.data.info;
            this.num = res.data.data.number
            if(this.data.length === 0) {
              this.msg = "暂无数据";
            }
            this.data.forEach(item => {
              let time =  new Date(item.use_time * 1000);
              let create_time = new Date(item.create_time * 1000);
              item.is_forbidden = `${item.is_forbidden}`
              item.use_time = time.getFullYear()+'/'+ (time.getMonth()-0+1)+'/'+time.getDate() + ' ' + time.getHours()+':'+time.getMinutes();
              item.create_time = create_time.getFullYear()+'/'+ (create_time.getMonth()-0+1)+'/'+create_time.getDate() + ' ' + create_time.getHours()+':'+create_time.getMinutes()
            })
            this.data.forEach(item => {
              if(item.is_publish === "0") {
                item.is_publish = "未发布"
              } else if(item.is_publish === "1") {
                item.is_publish = "已绑定"
              } else if(item.is_publish === "2") {
                item.is_publish = "已上传代码"
              } else if(item.is_publish === "3") {
                item.is_publish = "审核中"
              } else if(item.is_publish === "4") {
                item.is_publish = "已上线"
              }
            })
          } else {
            this.data = "暂无数据"
            const content = res.data.msg_test
            this.$Modal.error({
              title: "获取失败",
              content: content
            });
          }
        });
      },
      pageNnumber(val) {
        this.current = val
        this.getAppList(this.current, this.currentPage);
      },
      pageRow(val) {
        this.currentPage = val
        this.getAppList(this.current, this.currentPage);
      },
    },
    created() {
      this.getAppList()
    },
    components: {
      bread,
      page
    }
  }
</script>
<style scoped>
  .userList {
    width: 98%;
    margin: 0 auto;
  }

  .list {
    padding: 20px 0
  }

  table {
    width: 100%;
    font-size: 14px
  }

  th {
    padding: 10px 20px;
    background: #F5F5F5;
    text-align: center;
    border: 1px solid #E9ECEF;
  }

  .td>td {
    padding: 6px 10px;
    border-bottom: 1px solid #f2f2f2;
    text-align: center;
  }

  .info {
    color: white;
    background: #93B719;
    width: 85px;
    height: 30px;
    line-height: 30px;
    display: inline-block;
    text-align: center;
    border-radius: 4px;
  }

  td {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    border: 1px solid #E9ECEF;
    background: white;
  }

  .td_data {
    width: 100%;
    padding: 20px 10px;
    text-align: center;
    border-bottom: 1px solid #f2f2f2;
  }

  a:hover {
    opacity: 0.6;
    text-decoration: none
  }

  .ter_page {
    text-align: center;
    margin-top: 40px;
  }
  .people_info{
    font-size: 1em;
    font-weight: normal;
    color: #444;
    letter-spacing: 0.2em;
    margin-left: 5px;
  }
  .nickname{
    color: black;
  }
  .nickname:hover{
    opacity: 1;
    color: #549ef2;
  }
</style>



